/**
 * Custom Loader for UI Library
 * This file won't be overwritten by Stencil builds
 */

/**
 * Define custom elements and load required assets
 * @param win - Window object (optional, defaults to window)
 */
export const defineCustomElements = (win: any = window) => {
  if (typeof win === 'undefined') return Promise.resolve();
  
  // Load CSS styles dynamically
  const loadStyles = () => {
    const existingLink = win.document.querySelector('link[href*="ui-library.css"]');
    if (!existingLink) {
      const link = win.document.createElement('link');
      link.rel = 'stylesheet';
      link.href = '/ui-library/ui-library.css';
      win.document.head.appendChild(link);
    }
  };

  // Load JavaScript components dynamically
  const loadComponents = () => {
    const existingScript = win.document.querySelector('script[src*="ui-library.esm.js"]');
    if (!existingScript) {
      const script = win.document.createElement('script');
      script.src = '/ui-library/ui-library.esm.js';
      script.type = 'module';
      win.document.head.appendChild(script);
    }
  };

  // Initialize everything
  loadStyles();
  loadComponents();

  return Promise.resolve();
};